*{
  list-style-type: none;
  font-family: 'Roboto', sans-serif;
}
body{
  box-sizing: border-box; 
  color: black;
  background-color: #121212;
  color: #EDEDED;
  padding: 5px;
  margin: 5px;
  padding-top: 60px;
  margin-left: 200px;
  margin-right: 300px;
  font-family: 'Roboto', sans-serif;
}
h1 a{
  color: #EDEDED;
}
.comment-count{
  color: #EDEDED;
}
.navbar {
  width: 100%;
  top: 0px;
  left: 0px;
  position: fixed;
  transition: top 0.3s;
  display: inline-flex;
  justify-content: flex-end;
  align-items: baseline;
}
.navbar li {
  padding: 8px;
  margin-right: 50px;
}
.nav-button{
  background-color: inherit;
  border-color: #EDEDED;
  border-width: 2px;
  color: #EDEDED;
  padding: 14px 32px;
  font-size: 1.1em;
}
.app {
  margin-left: 40px
}
.button{
  background-color: #2541e2;
  border: none;
  border-radius: 5%;
  height: 30px;
  color: white;
  padding: 16px 32px;
  text-decoration: none;
  padding: auto;
  text-align: center;
  line-height: 0px;
}
a {
  text-decoration: none;
  color: black;
}
.by-text{
  color: #4e4e4e;
}



/* loading spinner  */
.loader {
  margin: 100px auto;
  font-size: 25px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load5 1.1s infinite ease;
  animation: load5 1.1s infinite ease;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
